import { Component, Injector, ElementRef, NgModule, ViewEncapsulation, Inject, forwardRef } from '@angular/core';
import { CommonModule } from '@angular/common';
import { OSharedModule } from '../../../shared';
import { OBarMenuComponent } from '../o-bar-menu.component';
import { OBaseMenuItemClass } from '../o-base-menu-item.class';

export const DEFAULT_INPUTS_O_BAR_MENU_GROUP = [
  ...OBaseMenuItemClass.DEFAULT_INPUTS_O_BASE_MENU_ITEM
];

@Component({
  moduleId: module.id,
  selector: 'o-bar-menu-group',
  template: `
    <div class="fake-li" [class.fake-li-hover]="isHovered">
      <label [attr.for]="id" class="toggle mat-list-item">
        <mat-icon class="o-bar-menu-group-icon mat-24">{{ icon }}</mat-icon>
        {{ title | oTranslate }}
        <mat-icon class="o-bar-menu-group-icon-arrow" *ngIf="!checkbox.checked">chevron_right</mat-icon>
        <mat-icon class="o-bar-menu-group-icon-arrow" *ngIf="checkbox.checked">expand_more</mat-icon>
      </label>
      <a class="o-bar-menu-group-title">
        <mat-icon class="o-bar-menu-group-icon mat-24">{{ icon }}</mat-icon> {{ title | oTranslate }}
        <mat-icon class="o-bar-menu-group-icon-arrow">chevron_right</mat-icon>
      </a>
      <input [attr.id]="id" type="checkbox" #checkbox />
      <div *ngIf="!disabled" class="fake-ul mat-primary mat-elevation-z4">
        <ng-content></ng-content>
      </div>
    </div>
  `,
  styles: [`
    .o-bar-menu-group[disabled=true] .fake-li{opacity:.5}.o-bar-menu-group[disabled=true] .fake-li a.o-bar-menu-group-title{cursor:default}.o-bar-menu-group .fake-li .o-bar-menu-group-icon,.o-bar-menu-group .fake-ul .o-bar-menu-group-icon{display:inline-block;margin:0 10px 0 0}.o-bar-menu-group .fake-li .o-bar-menu-group-icon ~ .o-bar-menu-group-title,.o-bar-menu-group .fake-ul .o-bar-menu-group-icon ~ .o-bar-menu-group-title{padding-left:0}.o-bar-menu-group .fake-li .o-bar-menu-group-icon-arrow,.o-bar-menu-group .fake-ul .o-bar-menu-group-icon-arrow{margin-left:auto}
  `],
  inputs: DEFAULT_INPUTS_O_BAR_MENU_GROUP,
  encapsulation: ViewEncapsulation.None,
  host: {
    '[class.o-bar-menu-group]': 'true',
    '[attr.disabled]': 'disabled'
  }
})
export class OBarMenuGroupComponent extends OBaseMenuItemClass {

  public static DEFAULT_INPUTS_O_BAR_MENU_GROUP = DEFAULT_INPUTS_O_BAR_MENU_GROUP;
  id: string;

  constructor(
    @Inject(forwardRef(() => OBarMenuComponent)) protected menu: OBarMenuComponent,
    protected elRef: ElementRef,
    protected injector: Injector
  ) {
    super(menu, elRef, injector);
    this.id = 'm_' + String((new Date()).getTime() + Math.random());
  }
}

@NgModule({
  declarations: [OBarMenuGroupComponent],
  imports: [CommonModule, OSharedModule],
  exports: [OBarMenuGroupComponent]
})
export class OBarMenuGroupModule {
}
